import DashboardWidget from 'flarum/admin/components/DashboardWidget';
import Button from 'flarum/common/components/Button';
export default class SeoWidget extends DashboardWidget {
  oninit(vnode) {
    super.oninit(vnode);
    this.needsReview = false;
    if (typeof app.data.settings.seo_review_settings === "undefined") {
      this.needsReview = true;
    }

    // Date passed?
    if (!this.needsReview && Math.floor(Date.now() / 1000) > app.data.settings.seo_review_settings) {
      this.needsReview = true;
    }
  }
  className() {
    return 'SeoWidget ' + (this.needsReview ? 'needs-review' : '');
  }
  content() {
    return m("div", null, m("i", {
      className: "fas fa-check seo-check-icon"
    }), " It's time to review your SEO settings!", Button.component({
      className: '',
      icon: 'far fa-thumbs-up',
      onclick: () => m.route.set("extension/vadkuz-flarum2-seo")
    }, 'Do the health-check!'));
  }
}
flarum.reg.add('vadkuz-flarum2-seo', 'admin/components/SeoWidget', SeoWidget);