import Modal from 'flarum/common/components/Modal';
import Button from 'flarum/common/components/Button';
import saveSettings from 'flarum/admin/utils/saveSettings';
export default class RobotsModal extends Modal {
  oninit(vnode) {
    super.oninit(vnode);
    this.value = typeof app.data.settings.seo_robots_text === "undefined" ? '' : app.data.settings.seo_robots_text;
    this.startValue = this.value;
    this.closeText = 'Close';
    this.loading = false;
  }
  title() {
    return 'Custom robots.txt';
  }
  content() {
    return m("div", null, m("div", {
      className: "Modal-body"
    }, m('textarea', {
      className: "FormControl",
      value: this.value,
      placeholder: 'Add text to the robots.txt',
      rows: 15,
      oninput: event => {
        this.change(event.target.value);
      }
    })), m("div", {
      style: "padding: 25px 30px; text-align: center;"
    }, this.closeDialogButton()));
  }
  change(value) {
    this.value = value;
    this.closeText = this.value !== this.startValue ? 'Save changes' : 'Close';
  }
  closeDialogButton() {
    return m(Button, {
      type: "submit",
      className: "Button Button--primary",
      loading: this.loading
    }, this.closeText);
  }

  // Close or save setting
  onsubmit(e) {
    if (this.value === this.startValue) {
      this.hide();
      return;
    }
    this.loading = true;
    let data = {};
    data.seo_robots_text = this.value;
    saveSettings(data).then(this.onsaved.bind(this));
  }
  onsaved() {
    this.hide();
  }
}
flarum.reg.add('vadkuz-flarum2-seo', 'admin/components/Modals/RobotsModal', RobotsModal);