import ExtensionPage from 'flarum/admin/components/ExtensionPage';
import SeoSettings from "../components/Forms/SeoSettings";
import HealthCheck from './HealthCheck';
import RegisterToSearchEngines from './RegisterToSearchEngines';
import SSLPage from './SSLPage';
import Button from 'flarum/common/components/Button';
import Sitemap from './Sitemap';
export default class SettingsPage extends ExtensionPage {
  content() {
    const page = m.route.param().page || 'health';
    return m("div", {
      className: "ExtensionPage-settings FlarumSEO"
    }, m("div", {
      className: "seo-menu"
    }, m("div", {
      className: "container"
    }, this.menuButtons(page))), m("div", {
      className: "container FlarumSeoPage-container"
    }, this.pageContent(page)));
  }

  // Return button menus
  menuButtons(page) {
    return [Button.component({
      className: "Button ".concat(page === 'health' ? 'item-selected' : ''),
      onclick: () => m.route.set(app.route('extension', {
        id: 'vadkuz-flarum2-seo'
      })),
      icon: 'fas fa-heartbeat'
    }, 'Health check'), Button.component({
      className: "Button ".concat(page === 'settings' ? 'item-selected' : ''),
      onclick: () => m.route.set(app.route('extension', {
        id: 'vadkuz-flarum2-seo',
        page: 'settings'
      })),
      icon: 'fas fa-cogs'
    }, 'SEO settings'), Button.component({
      className: "Button ".concat(page === 'sitemap' ? 'item-selected' : ''),
      onclick: () => m.route.set(app.route('extension', {
        id: 'vadkuz-flarum2-seo',
        page: 'sitemap'
      })),
      icon: 'fas fa-sitemap'
    }, 'Sitemap information'), Button.component({
      className: "Button ".concat(page === 'search-engines' ? 'item-selected' : ''),
      onclick: () => m.route.set(app.route('extension', {
        id: 'vadkuz-flarum2-seo',
        page: 'search-engines'
      })),
      icon: 'fas fa-search'
    }, 'Search engine information'), Button.component({
      className: "Button ".concat(page === 'ssl' ? 'item-selected' : ''),
      onclick: () => m.route.set(app.route('extension', {
        id: 'vadkuz-flarum2-seo',
        page: 'ssl'
      })),
      icon: 'fas fa-shield-alt'
    }, 'Set up SSL')];
  }
  pageContent(page) {
    if (page === 'search-engines') {
      return m(RegisterToSearchEngines, null);
    } else if (page === "settings") {
      return m(SeoSettings, null);
    } else if (page === "ssl") {
      return m(SSLPage, null);
    } else if (page === "sitemap") {
      return m(Sitemap, null);
    }

    // Default healthcheck
    return m(HealthCheck, null);
  }
}
flarum.reg.add('vadkuz-flarum2-seo', 'admin/Pages/SettingsPage', SettingsPage);